"use client"

import { useState, useCallback, useMemo } from "react"
import { useSession, signOut } from "next-auth/react"
import { MonthCalendar } from "@/components/month-calendar"
import { MonthReview } from "@/components/month-review"
import { LoginScreen } from "@/components/login-screen"
import { Button } from "@/components/ui/button"
import { ChevronLeft, ChevronRight, BookOpen, LogOut, Loader2 } from "lucide-react"
import { UserData } from "@/lib/storage"

export default function Home() {
  const { data: session, status } = useSession()
  const [currentYear, setCurrentYear] = useState(2025)
  const [currentMonth, setCurrentMonth] = useState(() => new Date().getMonth())
  const [showBack, setShowBack] = useState(false)

  const months = useMemo(() => {
    const isLeapYear = (currentYear % 4 === 0 && currentYear % 100 !== 0) || currentYear % 400 === 0
    return [
      { name: "Janeiro", days: 31 },
      { name: "Fevereiro", days: isLeapYear ? 29 : 28 },
      { name: "Março", days: 31 },
      { name: "Abril", days: 30 },
      { name: "Maio", days: 31 },
      { name: "Junho", days: 30 },
      { name: "Julho", days: 31 },
      { name: "Agosto", days: 31 },
      { name: "Setembro", days: 30 },
      { name: "Outubro", days: 31 },
      { name: "Novembro", days: 30 },
      { name: "Dezembro", days: 31 },
    ]
  }, [currentYear])

  const handleLogout = useCallback(() => {
    signOut({ callbackUrl: "/" })
  }, [])

  const handlePrevMonth = () => {
    if (currentMonth === 0) {
      setCurrentYear(prev => prev - 1)
      setCurrentMonth(11)
    } else {
      setCurrentMonth(prev => prev - 1)
    }
    // Removido o reset para permitir navegar nos livros lidos
  }

  const handleNextMonth = () => {
    if (currentMonth === 11) {
      setCurrentYear(prev => prev + 1)
      setCurrentMonth(0)
    } else {
      setCurrentMonth(prev => prev + 1)
    }
    // Removido o reset para permitir navegar nos livros lidos
  }

  if (status === "loading") {
    return (
      <div className="flex min-h-screen items-center justify-center bg-background">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    )
  }

  if (!session) return <LoginScreen onLogin={function (userData: UserData): void {
    throw new Error("Function not implemented.")
  } } />

  return (
    <main className="min-h-screen bg-background p-4 md:p-8">
      <div className="mx-auto max-w-6xl">
        <div className="mb-8 flex items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="rounded-lg bg-primary/10 p-2">
              <BookOpen className="h-6 w-6 text-primary" />
            </div>
            <div>
              {/* TÍTULO FIXO CONFORME SOLICITADO */}
              <h1 className="font-serif text-2xl font-bold text-foreground md:text-3xl">
                Calendário Literário {currentYear}
              </h1>
              <p className="text-sm text-muted-foreground">Olá, {session.user?.name}</p>
            </div>
          </div>

          <div className="flex items-center gap-2">
            <Button variant="outline" size="icon" onClick={handleLogout} className="h-9 w-9 bg-transparent">
              <LogOut className="h-4 w-4" />
            </Button>
            <Button variant="outline" size="icon" onClick={handlePrevMonth} className="h-9 w-9 bg-transparent">
              <ChevronLeft className="h-4 w-4" />
            </Button>
            <Button variant="outline" size="icon" onClick={handleNextMonth} className="h-9 w-9 bg-transparent">
              <ChevronRight className="h-4 w-4" />
            </Button>
          </div>
        </div>

        <div className="mt-6 flex justify-center mb-8">
          <Button onClick={() => setShowBack(!showBack)} variant="secondary" className="gap-2 px-6">
            {showBack ? "← Ver Calendário" : "Ver Livros Lidos →"}
          </Button>
        </div>

        <div className="relative">
           {!showBack ? (
              <MonthCalendar
                month={months[currentMonth].name}
                days={months[currentMonth].days}
                year={currentYear}
                userEmail={session.user?.email || ""}
                monthIndex={currentMonth}
              />
           ) : (
              <MonthReview 
                month={months[currentMonth].name} 
                userEmail={session.user?.email || ""} 
                monthIndex={currentMonth}
                year={currentYear}
              />
           )}
        </div>
      </div>
    </main>
  )
}