"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Star, BookMarked, Loader2, CalendarDays } from "lucide-react"
import { getReadingData } from "@/lib/api-client"

interface MonthReviewProps {
  month: string
  userEmail: string
  monthIndex: number
  year: number
}

export function MonthReview({ month, userEmail, monthIndex, year }: MonthReviewProps) {
  const [completedBooks, setCompletedBooks] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [covers, setCovers] = useState<Record<string, string>>({})
  const [ratings, setRatings] = useState<Record<string, number>>({})
  const formatDate = (dateString: string) => {
    if (!dateString) return ""
    const date = new Date(dateString)
    return date.toLocaleDateString('pt-BR', { timeZone: 'UTC' })
  }

  useEffect(() => {
    async function loadFinishedBooks() {
      if (!userEmail) return
      setLoading(true)
      try {
        const data = await getReadingData(userEmail, year, monthIndex + 1)
        
        const finished = data.filter((r: any) => {
          if (!r.end_date || r.status !== "finalizado") return false;
          const dateEnd = new Date(r.end_date);
          const endMonth = dateEnd.getUTCMonth() + 1;
          const endYear = dateEnd.getUTCFullYear();
          return endMonth === (monthIndex + 1) && endYear === year;
        })
        
        const uniqueBooks = Array.from(new Set(finished.map((a: any) => a.book_name)))
          .map(name => finished.find((a: any) => a.book_name === name))
          
        setCompletedBooks(uniqueBooks)
      } catch (err) {
        console.error("Erro ao carregar livros:", err)
      } finally {
        setLoading(false)
      }
    }
    loadFinishedBooks()
  }, [userEmail, monthIndex, year])

  if (loading) {
    return (
      <Card className="flex h-64 items-center justify-center border-accent/20 bg-card shadow-lg">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </Card>
    )
  }

  return (
    <Card className="border-accent/20 bg-card p-6 shadow-lg md:p-10">
      <div className="mb-8 text-center">
        <div className="mb-2 flex items-center justify-center gap-2">
          <BookMarked className="h-6 w-6 text-primary" />
          <h2 className="font-serif text-2xl font-bold text-foreground md:text-3xl">Lidos em {month}</h2>
        </div>
        <p className="text-sm text-muted-foreground">Sua jornada literária em {year}</p>
      </div>

      <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
        {completedBooks.length === 0 ? (
          <div className="col-span-full py-10 text-center">
            <p className="text-muted-foreground">Nenhum livro finalizado neste mês.</p>
          </div>
        ) : (
          completedBooks.map((book, idx) => (
            <div key={idx} className="group relative rounded-lg border border-border bg-background/50 p-4 transition-all hover:border-primary/40">
              <div className="flex gap-4">
                <div className="h-40 w-28 shrink-0 overflow-hidden rounded-md border bg-muted shadow-inner">
                  {covers[book.book_name] ? (
                    <img src={covers[book.book_name]} alt="Capa" className="h-full w-full object-cover" />
                  ) : (
                    <div className="flex h-full flex-col items-center justify-center text-muted-foreground p-2 text-center">
                      <BookMarked size={20} className="mb-1 opacity-20" />
                      <span className="text-[8px] uppercase font-bold leading-tight">{book.book_name}</span>
                    </div>
                  )}
                </div>

                <div className="flex flex-1 flex-col justify-between">
                  <div className="space-y-1">
                    <h3 className="text-sm font-black uppercase text-foreground leading-tight">
                      {book.book_name}
                    </h3>
                    
                    <div className="flex flex-col gap-0.5 pt-1">
                      <div className="flex items-center gap-1.5 text-[10px] text-muted-foreground font-medium">
                        <span className="w-10 text-[9px] font-bold text-primary/70 uppercase">Início:</span>
                        <span>{formatDate(book.start_date)}</span>
                      </div>
                      <div className="flex items-center gap-1.5 text-[10px] text-muted-foreground font-medium">
                        <span className="w-10 text-[9px] font-bold text-destructive/70 uppercase">Fim:</span>
                        <span>{formatDate(book.end_date)}</span>
                      </div>
                    </div>

                    <div className="pt-2">
                      <Input
                        placeholder="Link da imagem da capa..."
                        value={covers[book.book_name] || ""}
                        onChange={(e) => setCovers(prev => ({ ...prev, [book.book_name]: e.target.value }))}
                        className="h-6 text-[9px] bg-background/50 border-dashed"
                      />
                    </div>
                  </div>
                  
                  <div className="mt-2">
                    <p className="mb-1 text-[9px] text-muted-foreground font-bold uppercase tracking-wider">Avaliação</p>
                    <div className="flex gap-0.5">
                      {[1, 2, 3, 4, 5].map((star) => (
                        <Star
                          key={star}
                          onClick={() => setRatings(prev => ({ ...prev, [book.book_name]: star }))}
                          className={`h-4 w-4 cursor-pointer transition-all ${
                            star <= (ratings[book.book_name] || 0) 
                              ? "fill-primary text-primary" 
                              : "text-border hover:text-primary/30"
                          }`}
                        />
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          ))
        )}
      </div>
    </Card>
  )
}