import { executeQuery } from "@/lib/db";
import { NextResponse } from "next/server";

export async function GET(request: Request) {
  const { searchParams } = new URL(request.url);
  const email = searchParams.get("email");
  const year = Number(searchParams.get("year"));
  const month = Number(searchParams.get("month"));

  if (!email) return NextResponse.json({ data: [] });

  try {
    // Busca livros do mês atual OU qualquer livro que ainda esteja com status 'lendo'
    const query = `
      SELECT * FROM reading_data 
      WHERE email = $1 
      AND (
        (year = $2 AND month = $3) 
        OR 
        (status = 'lendo')
      )
      ORDER BY start_date ASC`;
    
    const rows = await executeQuery(query, [email, year, month]);
    return NextResponse.json({ data: rows });
  } catch (error) {
    return NextResponse.json({ error: "Erro ao buscar" }, { status: 500 });
  }
}

export async function POST(request: Request) {
  try {
    const body = await request.json();
    const { email, bookName, startDate, endDate, year, month, day, action, rating, coverUrl } = body;

    if (action === "START_READING") {
      const query = `
        INSERT INTO reading_data (email, book_name, start_date, year, month, day, status)
        VALUES ($1, $2, $3, $4, $5, $6, 'lendo')
      `;
      await executeQuery(query, [email, bookName, startDate, year, month, day]);
      return NextResponse.json({ success: true });
    }

    if (action === "FINISH_READING") {
      // Ao finalizar, atualizamos o mês/ano para o mês do calendário onde o usuário clicou
      // Assim o livro "muda" para o mês de encerramento e aparece no Review correto
      const queryUpdate = `
        UPDATE reading_data 
        SET end_date = $1, status = 'finalizado', month = $4, year = $5
        WHERE email = $2 AND book_name = $3 AND status = 'lendo'
      `;
      await executeQuery(queryUpdate, [endDate, email, bookName, month, year]);

      // Garante que o livro existe na tabela geral de books
      await executeQuery(`INSERT INTO books (name) VALUES ($1) ON CONFLICT (name) DO NOTHING`, [bookName]);

      // Cria a review inicial
      await executeQuery(`
        INSERT INTO books_reviews (user_email, book_name, rating, month, year)
        VALUES ($1, $2, $3, $4, $5) ON CONFLICT DO NOTHING`, 
        [email, bookName, 0, month, year]
      );

      return NextResponse.json({ success: true });
    }

    if (action === "UPDATE_REVIEW") {
      const query = `
        UPDATE books_reviews SET rating = $1, cover_url = $2 
        WHERE user_email = $3 AND book_name = $4
      `;
      await executeQuery(query, [rating, coverUrl, email, bookName]);
      return NextResponse.json({ success: true });
    }

    return NextResponse.json({ error: "Ação inválida" }, { status: 400 });
  } catch (error) {
    return NextResponse.json({ error: "Erro interno" }, { status: 500 });
  }
}